import fetch from '@/utils/fetch'
// 获取权限列表
export function getPermissionList (permissionId) {
  return fetch({
    url: '/authmanage/findPermissionList',
    method: 'get',
    params: {permissionId}
  })
}
// 添加权限
export function addPermission ({applicationKey, displayOrder, extendInfo, frontEndPortKey, isNavigation, isScope, meanUrl, parentId, permissionCode, permissionName, scopeType}) {
  return fetch({
    url: '/authmanage/addPermission',
    method: 'post',
    data: {
      applicationKey,
      displayOrder,
      extendInfo,
      frontEndPortKey,
      isNavigation,
      isScope,
      meanUrl,
      parentId,
      permissionCode,
      permissionName,
      scopeType
    }
  })
}
// 编辑权限
export function updateAuth ({permissionId, applicationKey, displayOrder, extendInfo, frontEndPortKey, isNavigation, isScope, meanUrl, parentId, permissionCode, permissionName, scopeType}) {
  return fetch({
    url: '/authmanage/updateAuth',
    method: 'post',
    data: {
      permissionId,
      applicationKey,
      displayOrder,
      extendInfo,
      frontEndPortKey,
      isNavigation,
      isScope,
      meanUrl,
      parentId,
      permissionCode,
      permissionName,
      scopeType
    }
  })
}
// 移除权限
export function removeRight (permissionId) {
  return fetch({
    url: '/authmanage/deleteAuth',
    method: 'post',
    data: {permissionId}
  })
}



