<template>
  <div class="op-content-info-frame">
    <slot></slot>
    <slot name="rightbar"></slot>
  </div>
</template>

<script>
  export default {
    name: 'op-content-info-frame',
    props: {
      height: {
        type: Number,
        default: 30
      }
    },
    mounted: function () {
    }
  }
</script>

<style lang="less">
  .op-content-info-frame {
    background: #F5F5F5;
    font-size: 14px;
    padding: 10px 0 10px 10px;
  }
</style>



