<style lang="less" scoped>

</style>
<template>
  <div class="permissions">
    <info-frame>
    <span>
        当前总条数
      <op-badge>{{data.length}}</op-badge>
      条
    </span>
    <span><el-button type="primary" size='small' @click='increVisible = true'>添加</el-button></span>
    </info-frame>
    <grid-frame>
      <template>
         <tree-table 
          :items.sync='data' 
          :columns='columns'
          @on-row-click='rowClick'
          v-loading='listLoading'
        ></tree-table>
      </template>
    </grid-frame>
     <!-- 新增权限 -->
      <el-dialog title="添加权限" :visible.sync="increVisible"  width="42%" class="w800" @close="clearForm('ruleForm')">
          <el-form :inline="true" ref="ruleForm" :model="ruleForm" :rules="addrules" label-width="160px"  class="demo-form-inline">
              <el-form-item label="权限编码" prop="permissionCode">
                  <el-input v-model="ruleForm.permissionCode" placeholder="请输入"></el-input>
              </el-form-item>
              <el-form-item label="权限名称" prop="permissionName">
                  <el-input v-model="ruleForm.permissionName" placeholder="请输入"></el-input>
              </el-form-item>
              <el-form-item label="菜单路由" prop="permissionRoute">
                  <el-input v-model="ruleForm.permissionRoute" placeholder="请输入"></el-input>
              </el-form-item>
              <el-form-item label="菜单排序" prop="permissionSort">
                  <el-input type="number" v-model="ruleForm.permissionSort" placeholder="请输入"></el-input>
              </el-form-item>
              <el-form-item label="扩展信息" prop="extendInfo">
                  <el-input v-model="ruleForm.extendInfo" placeholder="请输入"></el-input>
              </el-form-item>
               <el-form-item label="网站端口" prop="frontkey" >
                <el-select v-model="ruleForm.frontkey" placeholder="请选择-网站端口" :disabled="addChild">
                    <el-option label="运营端" value="operate"></el-option>
                    <el-option label="城市端" value="city"></el-option>
                    <el-option label="商停集团版" value="pg"></el-option>
                    <el-option label="商停2.0" value="parking"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="是否导航" prop="isNav">
                  <el-select v-model="ruleForm.isNav" placeholder="请选择">
                      <el-option label="是" value="1"></el-option>
                      <el-option label="否" value="0"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="权限范围" prop="isScope">
                  <el-select v-model="ruleForm.isScope" placeholder="请选择">
                      <el-option label="是" value="1"></el-option>
                      <el-option label="否" value="0"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="范围类型" prop="scopeType">
                  <el-select v-model="ruleForm.scopeType" placeholder="请选择">
                      <el-option label="无" value="0"></el-option>
                      <el-option label="行政区划" value="1"></el-option>
                      <el-option label="组织架构" value="2"></el-option>
                  </el-select>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="increVisible=false">取 消</el-button>
              <el-button type="primary" @click="add('ruleForm')">确 定</el-button>
            </span>
      </el-dialog>
  </div>
</template>
<script>
  import { mapGetters } from 'vuex'
  import { getPermissionList, addPermission, updateAuth, removeRight } from '@/api/platform/permission'
  import {parseTime} from '@/utils/filters'
  import opBadge from 'components/base/opBadge.vue'
  import infoFrame from 'components/base/infoFrame.vue'
  import gridFrame from 'components/base/gridFrame.vue'
  import pagination from 'components/base/pagination/index.vue'
  import treeTable from 'components/base/treeTable/treetable.vue'
  function scopeTypeFilter (type) {
    const scopeTypes = {
      0: '无',
      1: '行政区划',
      2: '组织架构'
    }
    return scopeTypes[type]
  }
  function isFilter (type) {
    const Types = {
      0: '否',
      1: '是'
    }
    return Types[type]
  }
  export default {
    data () {
      return {
        totalCount: 20,
        increVisible: false,
        listLoading: false,
        addChild: false,
        ruleForm: {
          permissionCode: '',
          permissionName: '',
          permissionRoute: '',
          permissionSort: '',
          extendInfo: '',
          isNav: '',
          isScope: '',
          scopeType: '',
          frontkey: '',
          permissionId: '',
          isEdit: false,
          parentId: ''
        },
        addrules: {
          permissionCode: [
            {required: true, message: '请输入权限编码', trigger: 'blur'}
          ],
          permissionName: [
            {required: true, message: '请输入权限名称', trigger: 'blur'}
          ],
          permissionRoute: [
            {required: true, message: '请输入菜单路由', trigger: 'blur'}
          ],
          permissionSort: [
            {required: true, message: '请输入数字', trigger: 'blur'}
          ]
        },
        columns: [
          {title: '权限编码', key: 'permissionCode', width: '150'},
          {title: '权限名称', key: 'permissionName', width: '150'},
          {title: '状态', key: 'status', width: '150'},
          {title: '菜单路由', key: 'meanUrl', width: '100'},
          {title: '菜单排序', key: 'displayOrder', width: '100'},
          {title: '扩展信息', key: 'extendInfo', width: '150'},
          {title: '前端端口', key: 'frontEndPortKey', width: '100'},
          {title: '是否导航', key: 'isNavigation', width: '100', 'farmat': isFilter},
          {title: '是否范围', key: 'isScope', width: '100', 'farmat': isFilter},
          {title: '范围类型', key: 'scopeType', width: '100', 'farmat': scopeTypeFilter},
          {title: '更新时间', key: 'updateTime', width: '150', 'farmat': parseTime},
          {title: '操作', type: 'action', actions: [{type: 'text', text: '编辑'}, {type: 'text', text: '添加子集'}, {type: 'text', text: '删除'}], width: '150'}
        ],
        data: []
      }
    },
    created () {
      this.$store.dispatch('Get_CITYLIST')
      this.fetchData()
    },
    computed: {
      ...mapGetters(['pageNum', 'pageSize', 'cityList', 'permissionList']),
      theight () {
        return document.body.offsetHeight - 300
      }
    },
    watch: {
      pageSize () {
        this.fetchData()
      },
      pageNum () {
        this.fetchData()
      }
    },
    components: {infoFrame, gridFrame, opBadge, pagination, treeTable},
    methods: {
      async fetchData () {
        this.listLoading = true
        const permissionId = ''
        const res = await getPermissionList(permissionId)
        this.listLoading = false
        this.data = res.returnObject
      },
      rowClick (data, ev, index, text) {
        console.log(data, index, ev, text)
        if (text === '编辑') {
          this.ruleForm.permissionCode = data.permissionCode
          this.ruleForm.permissionName = data.permissionName
          this.ruleForm.permissionRoute = data.meanUrl
          this.ruleForm.permissionSort = data.displayOrder
          this.ruleForm.extendInfo = data.extendInfo
          this.ruleForm.frontkey = data.frontEndPortKey
          this.ruleForm.isNav = data.isNavigation + ''
          this.ruleForm.isScope = data.isScope + ''
          this.ruleForm.scopeType = data.scopeType + ''
          this.increVisible = true
          this.ruleForm.permissionId = data.id
          if (data.parentId === '' || data.parentId === '0') {
            this.addChild = false
          } else {
            this.addChild = true
          }
          this.ruleForm.isEdit = true
        } else if (text === '删除') {
          this.removePermission(data.id)
        } else if (text === '添加子集') {
          this.ruleForm.parentId = data.id
          this.ruleForm.frontkey = data.frontEndPortKey
          this.addChild = true
          this.increVisible = true
        } else if (text === 'icon') {
          this.getChildren(data, index)
        }
      },
      // 查找子集
      async getChildren (data, index) {
        // const permissionId = data.id
        // const res = await getPermissionList(permissionId)
        // this.data[index].children = res.returnObject
      },
      // 添加或编辑权限
      async add (formName) {
        const valid = await this.$refs[formName].validate()
        if (valid) {
          let listQuery = {
            applicationKey: '1',
            displayOrder: this.ruleForm.permissionSort,
            extendInfo: this.ruleForm.extendInfo,
            frontEndPortKey: this.ruleForm.frontkey,
            isNavigation: this.ruleForm.isNav,
            isScope: this.ruleForm.isScope,
            meanUrl: this.ruleForm.permissionRoute,
            parentId: this.ruleForm.parentId,
            permissionCode: this.ruleForm.permissionCode,
            permissionName: this.ruleForm.permissionName,
            scopeType: this.ruleForm.scopeType
          }
          let res
          if (this.ruleForm.isEdit) {
            this.$set(listQuery, 'permissionId', this.ruleForm.permissionId)
            res = await updateAuth(listQuery)
          } else {
            res = await addPermission(listQuery)
          }
          this.$message({
            type: 'success',
            message: res.message
          })
          this.increVisible = false
          this.fetchData()
        }
      },
      // 删除权限
      async removePermission (permissionId) {
        let flag = await this.$confirm('是否永久删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        if (flag) {
          let res = await removeRight(permissionId)
          if (res) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.fetchData()
          }
        } else {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        }
      },
      // 清空表单
      clearForm (formName) {
        this.ruleForm.permissionCode = ''
        this.ruleForm.permissionName = ''
        this.ruleForm.permissionRoute = ''
        this.ruleForm.permissionSort = ''
        this.ruleForm.extendInfo = ''
        this.ruleForm.frontkey = ''
        this.ruleForm.isNav = ''
        this.ruleForm.isScope = ''
        this.ruleForm.scopeType = ''
        this.ruleForm.permissionId = ''
        this.ruleForm.isEdit = false
        this.ruleForm.parentId = ''
        this.addChild = false
      }
    }

  }
</script>




