<template>
    <div class="pagination" style="padding: 50px 0 20px;
    width: 100%;">
        <el-pagination 
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[10, 15, 30, 50]"
          :page-size="pageSize"
          layout="total,sizes, next, pager,prev"
          :total="totalCount"
          background>
        </el-pagination>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
      computed: {
        ...mapGetters([
          'pageNum',
          'pageSize',
          'totalCount'
        ])
      },
      methods: {
        handleSizeChange (val) {
          console.log(`每页 ${val} 条`)
          this.$store.dispatch('SET_PAGESIZE', parseInt(val))
        },
        handleCurrentChange (val) {
          console.log(`当前页: ${val}`)
          this.$store.dispatch('SET_PAGENUM', parseInt(val))
        }
      }
}
</script>






